/* common */
.flex {display: flex;}
.flex_j_center {justify-content: center;}
.flex_j_between {justify-content: space-between;}
.flex_a_center {align-items: center;}

.App {
  text-align: center;
}

.black-nav{
  display: flex;
  background-color: black;
  color: #fff;
  padding: 0 20px;
}

h5 {
  font-size: 18px;
  margin-top: 0;
  margin-bottom: 10px;
}
p{
  margin: 0;
}

div{
  box-sizing: border-box;
}
.list{
  position: relative;
  padding: 15px;
  text-align: left;
  border-bottom: 1px solid grey;
}
.btn_wrap{
  border-bottom: 1px solid #000;
}
.list .btn_wrap {
  justify-content: flex-end;
  border-bottom: 0;
}
.input_wrap {
  margin-top: 25px;
  
}
.btn {
  font-weight: bold;
}
.btn_title_change {
  width:150px;
  text-align: center;
  display: block;
  cursor: pointer;
  padding: 10px;
  border-radius: 5px;
  background-color: skyblue;
  margin: 20px auto;
  border: 0;
}
.btn_like {
  cursor: pointer;
  user-select:none
}
.btn_delete {
  background-color: #000;
  color: #fff;
  border: 0;
  padding: 5px 10px;
  position: absolute;
  top: 15px;
  right: 15px;
}
.btn_post {
  background-color: #000;
  color: #fff;
  padding: 7px 10px;
  margin-left: 5px;
}
.post_list {
  border-radius: 0;
  line-height: 30px;
  height: 30px;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}



/* modal */
.modal{
  margin-top : 20px;
  padding : 20px;
  background : #eee;
  text-align : left;
}